<template>
      <div class="background">
<div class="wrapper fadeInDown">
  <div id="formContent">
    <!-- Tabs Titles -->

    <!-- Icon -->
    <div class="fadeIn first logo">
      <div class="text-center">
        <img src="@/assets/logo.png" id="icon" alt="User Icon" />
      </div>
      <div class="text-center pb-4">
        <b id="title">0xsp Mongoose</b>

      </div>
    </div>

    <!-- Login Form -->
    <form @submit.prevent="login()">
      <input type="text" id="login" class="fadeIn second" v-model="fields.username" name="username" placeholder="Username">
      <input type="password" id="password" class="fadeIn third" v-model="fields.password" name="password" placeholder="Password">
      <input type="submit" class="fadeIn fourth" value="Log In">
    </form>

  </div>
</div>
      </div>
</template>

<script>
export default {
  data () {
    return {
      fields:{},
      username:'',
    } 
  },
  methods: {
    login(){
      this.axios.post('/auth',this.fields).then(response => {
        if(response.data.accessToken){
          localStorage.setItem('x-access-token',response.data.accessToken);
          this.$router.push('/dashboard');
          location.reload();
        }else{
          this.$toasted.show("Check Your Username or Password and try again!", { 
            theme: "toasted-primary", 
            position: "top-center", 
            duration : 1500,
            type:'error'
      });          
        }
      })
      .catch(err => {
        console.log(err.response);
      });
    }
  },
  created(){

}
};
</script>

